
html,
body {
  overflow: hidden;
  margin: 0;
  padding: 0;
}
a {
  outline: none;
  text-decoration: none;
  background-color: transparent;
}
a:hover,
a:active {
  outline-width: 0;
  text-decoration: none;
}
#app {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.main {
  position: absolute;
  width: 100%;
  height: 100%;
}
.main .mask {
  position: absolute;
  top: 46px;
  width: 100%;
  height: 41%;
  color: #fff;
}
.main .mask .sign {
  width: 80px;
  height: 80px;
  background-color: #e67c25;
  -webkit-box-shadow: 0 0 4px 4px #c1bb98;
          box-shadow: 0 0 4px 4px #c1bb98;
  border-radius: 50%;
  text-align: center;
  line-height: 80px;
  margin: 20% auto;
}
.main .mask .points {
  background-color: #58aec4;
  opacity: 0.8;
  position: absolute;
  top: 20px;
  height: 28px;
  line-height: 28px;
  font-size: 12px;
  text-indent: 6px;
  border-bottom-left-radius: 14px;
  border-top-left-radius: 14px;
  padding-right: 4px;
  right: 0;
  font-weight: 500;
}
.main .mask .points span {
  color: #f60101;
  margin-left: 5px;
}
.main .mask .points + .day {
  top: 64px;
}
.main .calendars {
  position: absolute;
  top: 48%;
  bottom: 0;
  min-height: 49%;
}
.inline-calendar thead {
  border-bottom: 1px solid #4eccc4;
}
.inline-calendar thead th {
  padding-bottom: 10px;
}
.statement > div {
  position: absolute;
  width: 100%;
}
.statement .header {
  top: 0;
  height: 40px;
}
.statement .header span {
  float: left;
  color: #0071BC;
  line-height: 40px;
  padding-left: 20px;
  font-size: 16px;
}
.statement .mainPop,
.statement .main {
  top: 46px;
  bottom: 45px;
}
.statement .mainPop div,
.statement .main div {
  padding: 5px 15px;
  word-break: break-all;
}
.statement .footer {
  bottom: 0;
  height: 45px;
}
.statement .footer span {
  float: right;
  color: #4eccc4;
  line-height: 40px;
  padding: 0 20px;
  font-size: 16px;
}
.notice {
  height: 44% !important;
}
.vux-alert .weui-dialog {
  width: 70%;
  border-radius: 10px;
}
.vux-alert .weui-dialog .weui-dialog__hd {
  padding: 0.8em 1.6em;
  border-bottom: 1px solid #f37505;
}
.vux-alert .weui-dialog .weui-dialog__ft {
  margin: 6px 10px;
  border-radius: 10px;
  background: #F37505;
}
.vux-alert .weui-dialog .weui-dialog__ft a:active {
  background: #F37505;
  color: #fff;
  border-radius: 10px;
}
.vux-alert .weui-dialog .weui-dialog__ft:after {
  border: none;
}
.vux-alert .weui-dialog .weui-dialog__bd {
  padding: 0.8em 1.6em;
  color: #4b4b4b;
}
.vux-confirm .weui-dialog {
  width: 70%;
  border-radius: 10px;
}
.vux-confirm .weui-dialog .weui-dialog__hd {
  padding: 0.8em 1.6em;
  border-bottom: 1px solid #f37505;
}
.vux-confirm .weui-dialog .weui-dialog__bd {
  padding: 0.8em 1.6em;
  color: #4b4b4b;
}
.vux-confirm .weui-dialog .weui-dialog__ft a:nth-child(1) {
  color: #000;
  background: #e1e1e175;
}
.vux-confirm .weui-dialog .weui-dialog__ft a:nth-child(2) {
  background: #F37505;
}
.inline-calendar tbody td {
  padding: 0;
}
.inline-calendar tbody td > span.vux-calendar-each-date {
  width: 100%;
  text-align: left;
  padding: 0 0 40px 5px;
  border-radius: 0.1px;
  -webkit-box-shadow: 0 0 0 0.2px #9a9b97;
          box-shadow: 0 0 0 0.2px #9a9b97;
}
.inline-calendar .current::after {
  content: url(data:image/png;base64,);
  height: 24px;
  position: absolute;
  bottom: 8%;
  right: 6%;
}
.signMask {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  width: 100%;
  height: 100%;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: rgba(0, 0, 0, 0.5);
}
.signMask .con {
  width: 65%;
  text-align: center;
  background: #fff;
  border-radius: 10px;
}
.signMask .con .title p {
  margin: 10px 0;
}
.signMask .con .title .titConY {
  font-size: 14px;
  text-align: left;
  padding: 0 15px;
}
.signMask .con .title .titCon {
  font-size: 16px;
  text-align: center;
  padding-top: 10px;
  border-top: 1px solid #ccc;
}
.signMask .con .list {
  width: 100%;
  color: #0c80fe;
  padding: 10px 0;
}
.signMask .con .list.top {
  border-top: 1px solid #ccc;
}
.signMask .con .list.bottom {
  border-top: 1px solid #ccc;
}
@media screen and (max-width: 360px) {
.notice {
    height: 50% !important;
}
.main .mask {
    height: 40%;
}
.main .calendars {
    position: absolute;
    bottom: 0;
    height: auto;
}
.inline-calendar tbody td > span.vux-calendar-each-date {
    padding: 0 0 34px 3px;
}
}
